
.player-controls {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 120px;
    margin-bottom: 28px;
  }
  
  .play-list {
    text-align: left;
  }
  
  .play-item {
    position: relative;
    padding: 5px;
    padding-left: 20px;
    list-style: none;
    opacity: .8;
    cursor: pointer;
    transition: .3s;
  }
  .now-playing {
    color: violet;
  }
  .play-item:hover {
    opacity: 1;
  }
  
  .play-item::before {
    content: "\2713";  
    position: absolute;
    left: 0;
    top: 2px;
    font-weight: 900;
  }
  
  .item-active::before {
    color: #C5B358;
  }
  
  .player-icon,
  .slider-icon,
  .change-quote {
    width: 32px;
    height: 32px;
    background-size: 32px 32px;
    background-position: center center;
    background-repeat: no-repeat;
    background-color: transparent;
    border: 0;
    outline: 0;
    opacity: .8;
    cursor: pointer;
    transition: .3s;  
  }
  
  .player-icon:hover,
  .slider-icon:hover,
  .change-quote:hover {
    opacity: 1;
  }
  
  .player-icon:active,
  .slider-icon:active,
  .change-quote:active {
    border: 0;
    outline: 0;  
    transform: scale(1.1);
  }
  
  .play {
    width: 40px;
    height: 40px;
    background-size: 40px 40px;
    background-image: url("../../assets/svg/play.svg");
  }
  
  .pause {
    background-image: url("../../assets/svg/pause.svg");
  }
  
  .play-prev {
    background-image: url("../../assets/svg/play-prev.svg");
  }
  
  .play-next {
    background-image: url("../../assets/svg/play-next.svg");
  }